@import "https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --body-font: "Nunito Sans", sans-serif;
  --light-mode-text: #111517;
  --light-mode-background: #fafafa;
  --light-mode-input: #858585;
  --white: #fff;
  --dark-mode-bg: #202c37;
  --dark-mode-elements: #2b3945;
  --purple: #8f2bec;
}

html {
  font-size: 62.5%;
}

body {
  background-color: var(--light-mode-background);
  color: var(--light-mode-text);
  font-family: var(--body-font);
  font-size: 1.8rem;
  position: relative;
}

.app {
  max-width: 1440px;
  margin: auto;
}

img {
  display: block;
}

a {
  color: var(--light-mode-text);
  text-decoration: none;
}

header {
  background-color: var(--white);
  padding: 3rem 5.1rem 2rem;
  font-size: 3rem;
  font-weight: 700;
  box-shadow: 0 0 3px 3px #00000008;
}

.searchBar {
  width: 40rem;
  margin: 1rem 1rem 1rem auto;
}

.searchBar input {
  width: 100%;
  background-color: var(--white);
  border: none;
  border-radius: 10px;
  padding: 2.4rem;
  box-shadow: 0 0 3px 3px #00000008;
}

.searchBar input::placeholder {
  opacity: .4;
  color: var(--light-mode-text);
  font-size: 1.6rem;
  font-weight: 600;
  font-family: var(--body-font);
}

.searchBar input[type="search"] {
  color: var(--light-mode-text);
  font-size: 1.6rem;
  font-family: var(--body-font);
  padding-left: 5rem;
}

.searchBar input:focus {
  outline: none;
}

.developer_cards_container {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 50px;
  margin: 4rem auto;
  display: flex;
}

.developer_card {
  max-width: 300px;
  background-color: var(--white);
  border-radius: 5px;
