  justify-content: center;
  align-items: center;
  display: flex;
  overflow: hidden;
  box-shadow: 0 0 3px 3px #00000008;
}

.developer_card:hover {
  transition: all .1s;
  transform: scale(1.05);
}

.developer_photo {
  width: 100%;
  height: 50%;
}

.developer_photo img {
  width: 100%;
  height: 100%;
}

.developer_info {
  width: 100%;
}

.devleloper_deatils {
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5px;
  margin: 2rem auto 1rem;
  display: flex;
}

.developer_name {
  margin-bottom: 1rem;
  font-size: 2.8rem;
  font-weight: 700;
}

.developer_company, .developer_location {
  text-align: center;
}

.developer_social_links {
  width: 100%;
  justify-content: space-around;
  margin-bottom: 1rem;
  padding: 1rem;
  display: flex;
}

.developer_social_links a {
  border-bottom: 1px solid var(--light-mode-text);
  font-weight: 600;
}

.developer_social_links a:hover {
  color: var(--purple);
  border-bottom: 1px solid var(--purple);
}

.pageNotFound {
  height: 80vh;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  font-weight: 600;
  display: flex;
}

.member_details {
  justify-content: space-evenly;
  margin-top: 3rem;
  padding: 20px;
  display: flex;
}

.member_image {
  max-width: 350px;
}

.member_image img {
  width: 100%;
  outline: 7px solid var(--dark-mode-bg);
  outline-offset: 1px;
  border-radius: 50%;
}

.member_info {
  max-width: 500px;
  flex-direction: column;
  align-items: center;
  display: flex;
}

