  display: flex;
}

.member_profile .memeber_username {
  margin-right: auto;
  font-size: 3rem;
  font-weight: 700;
}

.member_profile a {
  color: var(--light-mode-background);
  background-color: var(--light-mode-text);
  border: 1px solid var(--light-mode-text);
  border-radius: 15px;
  margin: auto 1px;
  padding: 1px 3rem;
  font-size: 2.4rem;
}

.member_follows {
  width: 70%;
  align-items: center;
  margin-top: 3rem;
  margin-bottom: 2rem;
  display: flex;
}

.member_follows p {
  cursor: pointer;
  margin: auto;
  font-weight: 600;
}

.member_name {
  text-transform: capitalize;
  font-size: 2rem;
  font-weight: 600;
}

.follows_container {
  width: 400px;
  height: 500px;
  border-radius: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: auto;
