  top: 10px;
  right: 15px;
}

.fellow {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.3rem;
  padding: 1rem;
  display: flex;
}

.fellow_details {
  align-items: center;
  gap: 1rem;
  display: flex;
}

.fellow_details img {
  width: 40px;
  height: auto;
  border-radius: 50%;
}

.fellow a {
  color: var(--light-mode-text);
  background-color: var(--light-mode-background);
  border-radius: 10px;
